html.demo-page,html.demo-page body{margin:0;background-color:transparent;color:#7f7f7f;font-family:"Noto Sans", sans-serif;box-sizing:border-box}html.demo-page nav{display:flex;align-items:center;justify-content:space-between;width:100%;margin-bottom:0;padding:0;background-color:rgba(0,0,0,0.8)}html.demo-page nav i{padding:.5rem}html.demo-page nav strong span{opacity:.7}html.demo-page main{max-width:700px;margin:0 auto;padding:1rem}html.demo-page h1{margin:.5em 0 .25em}html.demo-page img{width:100%;height:auto}html.demo-page img,html.demo-page p{margin:1rem 0}@media (min-width: 768px){html.demo-page section{display:grid;grid-template-columns:1fr 1.5fr;grid-column-gap:1rem}}html.demo-page .blockquote{position:relative;width:100%;max-width:500px;margin:2rem auto 3.5rem;padding:1.5rem 0;border-top:solid 1px;border-bottom:solid 1px;font-weight:700;z-index:1;opacity:.9}html.demo-page .blockquote h2{position:relative;font-size:40px;font-weight:800;line-height:1;margin:0}html.demo-page .blockquote::after{position:absolute;content:"‚Äù";color:#999;font-size:10rem;font-family:Georgia, sans-serif;line-height:0;bottom:-43px;right:30px}@media all and (min-width: 600px){html.demo-page .blockquote h2{font-size:60px}}html.demo-page .blockquote h4{position:relative;color:#888;font-size:1.4rem;font-weight:normal;line-height:1;margin:0;padding-top:20px;z-index:1}html.demo-page.second-page nav i:first-child{padding:.6rem .5rem .4rem}html.demo-page.second-page img{position:relative;top:1px}html.demo-page.second-page h2{opacity:.9}html.demo-page.second-page .blockquote::after{right:32px}html.demo-page.second-page .blockquote h2{font-family:-apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol}
